      left: auto; }
  .main-navigation li {
    float: left;
    position: relative; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Small menu. */
button.menu-toggle {
  height: 2.5rem;
  /*  40 / 16  */ }

.menu-toggle,
.main-navigation.toggled {
  padding-bottom: 0.9375rem;
  /*  15 / 16  */
  background: rgba(255, 255, 255, 0.9); }
  .menu-toggle ul.nav-menu,
  .main-navigation.toggled ul.nav-menu {
    display: block;
    /*  Hover Brackets Effect in Chrome/FF  */
    /* End hover brackets effect */ }
    .menu-toggle ul.nav-menu > li.menu-item,
    .main-navigation.toggled ul.nav-menu > li.menu-item {
      width: 100%; }
      .menu-toggle ul.nav-menu > li.menu-item a,
      .main-navigation.toggled ul.nav-menu > li.menu-item a {
        padding: 0.1875rem 0 0.1875rem 3.2rem;
        /*  3 / 16, the 3.2 is to get link text to line up with menu arrow icon  */ }
    .menu-toggle ul.nav-menu > li.menu-item a:hover,
    .menu-toggle ul.nav-menu > li.menu-item a:focus,
    .main-navigation.toggled ul.nav-menu > li.menu-item a:hover,
    .main-navigation.toggled ul.nav-menu > li.menu-item a:focus {
